.header { position: relative; z-index: 2; }

.header__nav { background: #a7c66d; font-size: 13px; color: #fff; height: 65px; line-height: 65px; }
.header__nav a { color: #fff; }
.header__nav a:hover { text-decoration: none; }
.header__nav .inner { background: #a7c66d; }
.header__nav .tel { margin-top: 12px; }

.nav a { display: inline-block; vertical-align: middle; padding: 0 9px; transition: background 0.3s; }
.nav a:hover { background: #799a2a; }

.header__main .inner { background: #fff; }
.header__main .inner:before { background: #fff; }

.logo img { width: 100%; height: auto; }


